import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import axios from 'axios';
import './Home.css';
import AddEdit from './AddEdit';
let sn = 0;
const Home = () => {
  const userEmail = localStorage.getItem('userEmail');
  console.log(userEmail);
  const [data, setData] = useState({});
  const [em, setEm] = useState({});
  const navigate = useNavigate();
  if(!userEmail){
  
  }
  //const [em] = useState(userEmail.replace(/[.@]/g, ''));
  useEffect(() => {
    // Check if the user's email is present in local storage
    if (!userEmail) {
      // If it's not, push the user to the Login page
      navigate('/');
    }
    else{
      setEm=userEmail.replace(/[.@]/g, '');
    }

    const fetchData = async () => {
      try {
        const response = await axios.get(
          `https://react-916d9-default-rtdb.firebaseio.com/conacts/${em}.json`,
        );
        if (response.data) { // Add this check to see if response.data is defined or not
          setData(response.data);
        }
      } catch (error) {
        toast.error(error.message);
      }
    };
    fetchData();
  }, [navigate]);

  const onDelete = (id) => {
    if (window.confirm('Are you sure you want to delete this user?')) {
      axios
        .delete(
          `https://react-916d9-default-rtdb.firebaseio.com/conacts/${em}/${id}.json`,
        )
        .then(() => {
          toast.success('User deleted successfully');
        })
        .catch((error) => {
          toast.error(error.message);
        });
    }
  };
  return (
    <div className="main-div splash_globe" >
      <div className="table-container">
        {Object.keys(data).length === 0 ? (
          <p>No data found</p>
        ) : (
          <table>
            <thead>
              <tr>
                <th>S.NO</th>
                <th>Name</th>
                <th>Email</th>
                <th>Contact</th>
                <th>Marital Status</th>
                <th>GCM</th>
                <th>office1</th>
                <th>office2</th>
                <th>office3</th>
                <th>salary</th>
                <th>Are you on Bench</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {Object.keys(data).map((id, index) => {
                return (
                  <tr key={id}>
                    <td>{sn = sn + 1}</td>
                    <td>{data[id].name}</td>
                    <td>{data[id].email}</td>
                    <td>{data[id].contact}</td>
                    <td>{data[id].maritalstatus}</td>
                    <td>{data[id].gcmlevel}</td>
                    <td>{data[id].office1}</td>
                    <td>{data[id].office2}</td>
                    <td>{data[id].office3}</td>
                    <td>{data[id].salary}</td>
                    <td>{data[id].benchstatus}</td>
                    <td>
                      <div className="button-container">
                        <Link to={`/update/${id}`}>
                          <button type="button" className="button">Edit</button>
                        </Link>
                        <button onClick={() => onDelete(id)} className="button">Delete</button>
                        <Link to={`/view/${id}`}>
                          <button className="button">View</button>
                        </Link>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );  
};
export default Home;
