import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './login.css';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const GOOGLE_AUTH_API_KEY = process.env.REACT_APP_GOOGLE_AUTH_API_KEY;

  const handleLogin = () => {
    axios.post(`https://www.googleapis.com/identitytoolkit/v3/relyingparty/verifyPassword?key=${GOOGLE_AUTH_API_KEY}`, { email, password })
      .then((response) => {
        console.log(response);
        console.log(response.data.email);
        localStorage.setItem('userEmail', response.data.email);
        navigate('/home',{ state:{isLoggedIn: true} });
      })
      .catch((error) => {
        console.error(error);
        console.log(GOOGLE_AUTH_API_KEY);
        console.log("using process" + process.env.REACT_APP_GOOGLE_AUTH_API_KEY);
        setError('Invalid email or password.');
      });

  };

  return (
    <div id="card">
  <div id="card-content">
    <div id="card-title">
      <h2>LOGIN</h2>
      <div className="underline-title"></div>
    </div>
    <div className="form">
      <label>
        Email
      </label>
      <input className="form-content" type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
      <div className="form-border"></div>
      <label>
        Password
      </label>
      <input className="form-content" type="password" value={password} onChange={(e) => setPassword(e.target.value)} required />
      <div className="form-border"></div>
      {error && <p>{error}</p>}
      <button id="submit-btn" onClick={handleLogin}>LOGIN</button>
    </div>
  </div>
</div>
  );
};

export default Login;
