import React,{useState,useEffect } from 'react';
import { toast } from 'react-toastify';
import './AddEdit.css';
import axios from 'axios';
import { Link, useNavigate, useParams } from 'react-router-dom';

const userEmail = localStorage.getItem('userEmail');
//const em = userEmail.replace(/[.@]/g, '');
const initialState = {
    emp:"",
    name:"",
    email:"",
    contact:"",
    maritalstatus:"",
    gcmlevel:"",
    office1:"N/A",
    office2:"N/A",
    office3:"N/A",
    dateofjoining:"",
    salary:"",
    benchstatus:"",
};

const AddEdit = () => {

    const [state, setState] = useState(initialState);
    const [data, setData] = useState({});
    const { emp, name, email, contact, dateofjoining, salary, benchstatus,maritalstatus, gcmlevel, office1,office2, office3 } = state;
    const navigate = useNavigate();
    const { id } = useParams();
    const [em, setEm] = useState({});


  useEffect(() => {
    if (!userEmail) {
      // If it's not, push the user to the Login page
      navigate('/');
    }
    else
    {
        setEm=userEmail.replace(/[.@]/g, '');
    }
  }, [navigate]);

    useEffect(() => {
        axios.get(`https://react-916d9-default-rtdb.firebaseio.com/conacts/${em}.json`)
            .then((response) => {
                if (response.data !== null) {
                    setData({ ...response.data });
                } else {
                    setData({});
                }
            })
            .catch((error) => {
                console.log(error);
            });
        
        return () => {
            setData({});
        }
    }, [id]);

    useEffect(() => {
        if (id) {
            setState({ ...data[id] });
        } else {
            setState({ ...initialState });
        }
        return () => {
            setState({ ...initialState });
        }
    }, [id, data]);

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setState({ ...state, [name]: value });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!emp || !name || !email || !contact) {
            toast.error("please provide value in each input field");
        } else {
            const newContact = {
                emp,
                name,
                email,
                contact,
                dateofjoining,
                salary,
                benchstatus,
                maritalstatus,
                gcmlevel,
                office1,
                office2,
                office3
            };

            if (!id) {
                axios.post(`https://react-916d9-default-rtdb.firebaseio.com/conacts/${em}.json`, newContact)
                    .then((response) => {
                        console.log(userEmail);
                       // console.log(em);
                        toast.success("user added successfully");
                    })
                    .catch((error) => {
                        console.log(userEmail);
                        //console.log(em);
                        toast.error("System error: " + error.message);
                    });
            } else {
                axios.put(`https://react-916d9-default-rtdb.firebaseio.com/conacts/${em}/${id}.json`, newContact)
                    .then((response) => {
                        toast.success("user updated successfully");
                    })
                    .catch((error) => {
                        toast.error("System error: " + error.message);
                    });
            }
        }


    //     try {
    //     const fireDb = getDatabase();
    //     set(ref(fireDb, 'conacts/'+id), state );      
    //     toast.success("Contact Added Successfully");
    //     setTimeout( ()=> history("/"), 500);
    // } catch (error) {
    //         toast.error("System error =>081-048-9");
    //     }

    
    //setTimeout( ()=> history("/"), 500);
    
 };

 return (
  <div style={{marginTop: "100px"}}>
  <form 
  style={{margin: "auto" , padding: "15px" , maxWidth: "400px" , alignContent:"center"}}
  onSubmit={handleSubmit}            
  >

      <label htmlFor="id"><b>Emp ID</b></label>
      <input
       type="number" 
       id="emp" 
       name="emp" 
       placeholder="Emp Id..." 
       value={emp || ""} 
       onChange={handleInputChange}
       />

      <label htmlFor="name"><b>Name </b></label>
      <input
       type="text" 
       id="name" 
       name="name" 
       placeholder="Your Name..." 
       value={name || ""} 
       onChange={handleInputChange}
       />
      
      <label htmlFor="emai"><b>Email</b></label>
      <input
       type="email" 
       id="email" 
       name="email" 
       placeholder="Your Email..." 
       value={email || ""} 
       onChange={handleInputChange}
       />

      <label htmlFor="contact"><b>Contact</b></label>
      <input
       type="number"
       id="contact" 
       name="contact" 
       placeholder="Your Contact..." 
       value={contact || ""} 
       onChange={handleInputChange}
       />                
        <lable htmlFor="maritalstatus"><b>Marital Status</b></lable>
        <br></br>
        <input
        type="radio"
        id="maritalstatus"
        name="maritalstatus"
        value={"married"}
        onChange={handleInputChange}
        />Married
          
      <input
        type="radio"
        id="maritalstatus"
        name="maritalstatus"
        value={"Unmarried"}
        onChange={handleInputChange}
        />Unmarried
        <br></br>

        <label htmlFor="gcmlevel"><b>Select GCM LEVEL</b></label>
        <select
        id="gcmlevel"
        name="gcmlevel"
        onChange={handleInputChange}
        >
          <option    
        value={"GCM-1"}
        >GCM-1
        </option>
        
        <option
        value={"GCM-2"}
        >GCM-2
        </option>
        <option
        value={"GCM-3"}
        >GCM-3
        </option>
        <option
        value={"GCM-4"}
        >GCM-4
        </option>
        <option
        value={"GCM-5"}
        >GCM-5
        </option>
        <option
        value={"GCM-6"}
        >GCM-6
        </option>
        </select>
        <label htmlFor="office"><b>Choose Your office Locations</b></label>
        <br></br>
        Pune:<input
        type="checkbox"
        id="office1"
        name="office1"
        value={"Pune"}
        onChange={handleInputChange}
        />
        &nbsp;&nbsp; Mumbai:<input
        type="checkbox"
        id="office2"
        name="office2"
        value={"Mumbai"}
        onChange={handleInputChange}
        />
        &nbsp;&nbsp; Chennai:<input
        type="checkbox"
        id="office3"
        name="office3"
        value={"Chennai"}
        onChange={handleInputChange}
        />
        <br></br>
        <label htmlFor="dateofjoining"><b>please Select date of Joining</b></label>
        <br></br>
        <input
        type="date"
        id="dateofjoining"
        name="dateofjoining"
        value={dateofjoining || ""}
        onChange={handleInputChange}
        />
        <br></br>
        <label htmlFor="salary"><b>Select Your exact salary</b></label>
        <br></br>
        <div>
  <input
    type="range"
    min="1"
    max="2000000"
    id="salary"
    name="salary"
    value={salary || "0"}
    onChange={handleInputChange}
    onInput={(e) => document.querySelector('#rangeValue').value = e.target.value}
  />
  <output id="rangeValue">{salary || "0"}</output>
</div>

        <br></br>
        <div>
         <b>Are you on bench?</b>
         <br></br>
          <input
          class="checkbox"
          type="checkbox"
          id="benchstatus"
          name="benchstatus"
          value={benchstatus || "NO"}
          onChange={handleInputChange}
          />
           <label htmlFor="benchstatus" className="toggle">
          <p>YES   NO</p>
          </label>
        </div>
      <input type="submit" value={id ? "update" : "save"}  />
  </form>
  <Link to={"/"} className="link">cancel</Link>
</div>
)
}
export default AddEdit
